import React, { useState } from 'react';
import {
  Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Chip, 
  Dialog, DialogTitle, DialogContent, DialogContentText, Typography, Box, IconButton, Tooltip,
  DialogActions, Button
} from '@mui/material';
import { ContentCopy, Download } from '@mui/icons-material';
import ReactMarkdown from 'react-markdown';

// As defined in Step 2
interface Command {
  id: string;
  name: string;
  usage: string;
  example: string;
  labels: string[];
  description: string;
  prompt: string;
}

interface CommandTableProps {
  commands: Command[];
}

const CommandTable: React.FC<CommandTableProps> = ({ commands }) => {
  const [selectedCommand, setSelectedCommand] = useState<Command | null>(null);
  const [open, setOpen] = useState(false);
  const [promptCopied, setPromptCopied] = useState(false);
  const [installCopied, setInstallCopied] = useState(false);

  const handleRowClick = (command: Command) => {
    setSelectedCommand(command);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setSelectedCommand(null);
    setPromptCopied(false);
    setInstallCopied(false);
  };

  const handleCopyPrompt = async () => {
    if (selectedCommand?.prompt) {
      await navigator.clipboard.writeText(selectedCommand.prompt);
      setPromptCopied(true);
      setTimeout(() => setPromptCopied(false), 2000);
    }
  };

  const handleCopyInstall = async () => {
    if (selectedCommand) {
      const installScript = `
# Create directory if it doesn't exist
mkdir -p ~/.gemini/commands/

# Download the command
curl -o ~/.gemini/commands/${selectedCommand.id}.toml https://gemini-cli-commands-hub.github.io/commands/${selectedCommand.id}.toml

echo "Command '${selectedCommand.name}' installed successfully!"
      `.trim();
      await navigator.clipboard.writeText(installScript);
      setInstallCopied(true);
      setTimeout(() => setInstallCopied(false), 2000);
    }
  };

  return (
    <>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="commands table">
          <TableHead>
            <TableRow>
              <TableCell sx={{ fontWeight: 'bold' }}>Command (Name)</TableCell>
              <TableCell sx={{ fontWeight: 'bold' }}>Description</TableCell>
              <TableCell sx={{ fontWeight: 'bold' }}>Labels</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {commands.map((command) => (
              <TableRow
                key={command.id}
                hover
                onClick={() => handleRowClick(command)}
                sx={{ cursor: 'pointer' }}
              >
                <TableCell component="th" scope="row">
                  {command.name}
                </TableCell>
                <TableCell>{command.description}</TableCell>
                <TableCell>
                  {command.labels.map(label => (
                    <Chip key={label} label={label} size="small" sx={{ mr: 0.5, mb: 0.5 }} />
                  ))}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Dialog open={open} onClose={handleClose} maxWidth="md" fullWidth>
        {selectedCommand && (
          <>
            <DialogTitle>{selectedCommand.name}</DialogTitle>
            <DialogContent dividers>
              <Box mb={2}>
                <Typography variant="h6" gutterBottom>Description</Typography>
                <DialogContentText>{selectedCommand.description}</DialogContentText>
              </Box>
              <Box mb={2}>
                <Typography variant="h6" gutterBottom>Usage</Typography>
                <DialogContentText component="code" sx={{ display: 'block', bgcolor: 'rgba(0,0,0,0.08)', p: 1, borderRadius: 1 }}>
                  {selectedCommand.usage}
                </DialogContentText>
              </Box>
              <Box mb={2}>
                <Typography variant="h6" gutterBottom>Example</Typography>
                <DialogContentText component="code" sx={{ display: 'block', bgcolor: 'rgba(0,0,0,0.08)', p: 1, borderRadius: 1 }}>
                  {selectedCommand.example}
                </DialogContentText>
              </Box>
              <Box>
                <Box display="flex" justifyContent="space-between" alignItems="center">
                  <Typography variant="h6" gutterBottom>Prompt</Typography>
                  <Tooltip title={promptCopied ? 'Copied!' : 'Copy Prompt'}>
                    <IconButton onClick={handleCopyPrompt} size="small">
                      <ContentCopy />
                    </IconButton>
                  </Tooltip>
                </Box>
                <Paper variant="outlined" sx={{ p: 2, '& pre': { whiteSpace: 'pre-wrap', wordBreak: 'break-all' } }}>
                  <ReactMarkdown>{selectedCommand.prompt}</ReactMarkdown>
                </Paper>
              </Box>
            </DialogContent>
            <DialogActions sx={{p: '16px 24px'}}>
              <Tooltip title={installCopied ? 'Copied!' : 'Copy install command to clipboard'}>
                <Button 
                  variant="contained" 
                  startIcon={<Download />} 
                  onClick={handleCopyInstall}
                >
                  Install Command
                </Button>
              </Tooltip>
            </DialogActions>
          </>
        )}
      </Dialog>
    </>
  );
};

export default CommandTable;