import React, { useState, useMemo } from 'react';
import {
  Container, Typography, TextField, Autocomplete, Box, createFilterOptions
} from '@mui/material';
import CommandTable from './components/CommandTable';
import allCommands from './commands.json';

// As defined in Step 2
interface Command {
  id: string;
  name: string;
  usage: string;
  example: string;
  labels: string[];
  description: string;
  prompt: string;
}

const App: React.FC = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedLabels, setSelectedLabels] = useState<string[]>([]);

  const allLabels = useMemo(() => {
    const labelsSet = new Set<string>();
    allCommands.forEach(command => {
      command.labels.forEach(label => labelsSet.add(label));
    });
    return Array.from(labelsSet);
  }, []);

  const filteredCommands = useMemo(() => {
    return allCommands.filter(command => {
      const searchTermLower = searchTerm.toLowerCase();
      
      const matchesSearchTerm = searchTerm === '' ||
        command.name.toLowerCase().includes(searchTermLower) ||
        command.description.toLowerCase().includes(searchTermLower) ||
        command.prompt.toLowerCase().includes(searchTermLower);

      const matchesLabels = selectedLabels.length === 0 ||
        selectedLabels.every(label => command.labels.includes(label));

      return matchesSearchTerm && matchesLabels;
    });
  }, [searchTerm, selectedLabels]);

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Typography variant="h3" component="h1" align="center" gutterBottom>
        Gemini CLI Commands Hub
      </Typography>
      <Typography variant="h6" align="center" color="text.secondary" sx={{ mb: 4 }}>
        Discover and share useful commands for Gemini CLI.
      </Typography>

      <Box sx={{ display: 'flex', gap: 2, mb: 4, flexWrap: 'wrap' }}>
        <TextField
          label="Search by keyword..."
          variant="outlined"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          sx={{ flexGrow: 1, minWidth: '300px' }}
        />
        <Autocomplete
          multiple
          id="labels-filter"
          options={allLabels}
          value={selectedLabels}
          onChange={(event, newValue) => {
            setSelectedLabels(newValue);
          }}
          filterOptions={createFilterOptions()}
          getOptionLabel={(option) => option}
          renderInput={(params) => (
            <TextField
              {...params}
              variant="outlined"
              label="Filter by labels"
              placeholder="Labels"
            />
          )}
          sx={{ flexGrow: 1, minWidth: '300px' }}
        />
      </Box>

      <CommandTable commands={filteredCommands as Command[]} />
    </Container>
  );
};

export default App;